* {
    font-family: cursive;
}

.flex {
    display: flex;
}

.flex-col {
    flex-direction: column;
}

.flex-row {
    flex-direction: row;
}

.justify-center {
    justify-content: center;
}

.justify-start {
    justify-content: start;
}

.items-start {
    align-items: flex-start;
}

.items-center {
    align-items: center;
}